/* Body
-------------------------------------------------- */
html {
  font-size: 62.5%;
}

body {
  color: #00273a;
  font-family: Calibri, Candara, Segoe, Segoe UI, Optima, Arial, sans-serif;
  line-height: 1.2;
  background-color: #ffffff;
  font-size: 1.4rem;
}

@media only screen and (min-width: 768px) {
