    font-size: 1.82rem;
  }
}

* {
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  -webkit-padding-start: 0;
}

/* Typo
-------------------------------------------------- */
h1, h2, h3, h4, h5, h6 {
  color: #33718f;
  font-weight: normal;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  line-height: 1;
}

h1 {
  font-size: 140%;
  font-weight: bold;
}

h2 {
  font-size: 130%;
  font-weight: bold;
}

h3 {
  font-size: 120%;
}

h4 {
  font-size: 115%;
}

h5 {
  font-size: 105%;
}

h6 {
  font-size: 100%;
}

a {
  color: #33718f;
  cursor: pointer;
  text-decoration: none;
}

a:hover {
  color: #1a5f81;
  text-decoration: underline;
}

