// -----------------------------------------------------------------------------
// Body
// -----------------------------------------------------------------------------
body {
  @include displayFlex();
  @include flexDirection(column);
  min-height: 100vh;

  /* body overflow is hidden to hide main scrollbar when modal window is open */
  &.modal-open {
      overflow: hidden;
  }
}


